@import url('https://fonts.googleapis.com/css?family=Libre+Baskerville&display=swap');

h1 {
    font-family: 'Libre Baskerville', serif;
    text-align: center;
    font-size: 24px;
    letter-spacing: .5rem;
}

.metronome {
    text-align: center;
    max-width: 375px;
    margin: 0 auto;
    padding: 30px;
}

.bpm-slider input {
    width: 100%;
    margin: 10px;
}

.beats-input {
    margin: 10px;
}

.beats-input input {
    text-align: center;
    font-size: 16px;
}

.metronome button {
    background: #c94d46;
    padding: 10px;
    border: 1px solid #832420;
    border-radius: 2px;
    width: 100px;
    color: #fff;
    font-size: 18px;
}

label {
    display: block;
    padding: 20px;
}

